/* Собственно сам слайдер */
.slider{

    position: relative;
   
    margin-bottom: 30px;
    margin-top: 30px;
    margin: auto;
}
/* Картинка мастабируется по отношению к родительскому элементу */
.slider .itemSl img {
    object-fit: cover;
    height: 100%;
    width: 100%;
    margin: auto;
    
}

/* Кнопки вперед и назад */
 .prev, .next {
    cursor: pointer;
    position: absolute;
    top: 25%;
    width: auto;
    padding: 110px 15px;
    color: white;
    font-weight: bold;
    font-size: 28px;
    transition: 0.6s ease;
    background-color: rgba(182, 179, 179, 0.397);
    border: none;
}


 .next {
    right: 0px;
}
.prev {
    left: 0px;
}


/* При наведении на кнопки добавляем фон кнопок */

 .prev:hover,
 .next:hover {
    background-color: rgba(0, 0, 0, 0.8);
}


/* Заголовок слайда */
.slideText {
    position: absolute;
    color: #fff;
    font-size: 35px;
    /* Выравнивание текста по горизонтали и по вертикали*/
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    /* Тень*/
    text-shadow: 1px 1px 1px #000, 0 0 1em #000;
}


/* Кружочки */

.slider-dots {
  text-align: center;
 
}
.slider-dots_item{
    cursor: pointer;
    height: 12px;
    width: 12px;
    margin: 0 2px;
    background-color: #ddd;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
}

.active,
.slider-dots_item:hover {
    background-color: #aaa;
}


/* Анимация слайдов */

.slider .itemSl {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
}

@-webkit-keyframes fade {
    from {
        opacity: .4
    }
    to {
        opacity: 1
    }
}

@keyframes fade {
    from {
        opacity: .4
    }
    to {
        opacity: 1
    }
}


